.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-family: Roboto, serif;
  font-style: normal;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo-nav {
  display: flex;
}

.header__logo {
  display: block;
}

.header-navigation {
  display: flex;
}

.header-navigation__list {
  text-transform: uppercase;
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.header-navigation__link {
  color: #060b35;
  white-space: nowrap;
  align-items: center;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.header-navigation__link:hover {
  color: #00acdc;
  transition-duration: .3s;
}

.header-navigation__link.is-active {
  color: #00acdc;
}

.header-navigation__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.header-navigation__link__item:first-child .header-navigation__link {
  margin-left: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding-inline: 16px;
  font-family: Roboto, serif;
  transition-duration: .3s;
}

.card__image {
  margin: 32px auto 40px;
  justify-content: center;
  width: 160px;
  height: 134px;
  margin-block: 32px 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price-label {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card:hover {
  transition-duration: .3s;
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
  transition-duration: .3s;
}

.card__buy-link {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-bottom: 16px;
  padding-block: 12px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
}

.card__buy-link:hover {
  color: #00acdc;
  background-color: #0000;
  border: 1px solid #00acdc;
  transition-duration: .3s;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.stars {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.reviews {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}
